<script>
    import Fa from 'svelte-fa';
    import { faTrash } from '@fortawesome/free-solid-svg-icons';

    export let extraData;
    console.log(extraData);
    let listeners = extraData['listeners']
	import Header from './components/header.svelte';
	import TextArea from './components/textArea.svelte';
    import { add_listener, delete_listener, update_listener } from './utils.js';
</script>

<main class="index">
    <Header user_info={extraData['current_user']} />
    <h1 class="listener space-around">Listeners</h1>
    {#each listeners as listener, index}
        <div class="listener">
            <div class="space-between">
                <div class="create_button">
                    <input type="text" value={listener[0]} on:input={e => update_listener("listen_for", e.target.value, "command", listener[1])}>
                    <span class="cust_minus">-</span>
                </div>
                <TextArea label_value="Update Command" bind_value={listener[1]} on:input={e => update_listener("command", e.target.value, "listen_for", listener[0])} style="background: white;" />
                <span class="cust_icon pointer" on:click={() => {
                    delete_listener(listener[0])
                    listeners = listeners.filter(listen_x => listen_x[0] != listener[0])
                }} role="presentation">
                    <Fa icon={faTrash} size='2x' />
                </span>
            </div>
        </div>
    {/each}
    <div class="listener space-around"><span class="cust_button pointer border" on:click={() => {
        add_listener();
        listeners = [...listeners, ['', '']]
    }} role="presentation">Add Listener</span></div>
</main>


